.card {
  background: whitesmoke;
  padding: 4px;
  height: 250px;
  margin: 1rem;
  position: relative;
  width: 250px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: #98c0e7;
}

img {
  height: 100%
}

.card:hover {
  -webkit-filter: drop-shadow(5px 5px 5px #222 );
  filter: drop-shadow(5px 5px 5px #222);
}

.card:active {
  -webkit-filter: drop-shadow(10px 10px 10px #222 );
  filter: drop-shadow(10px 10px 10px #222);
}
.header {
    padding-top: 125px;
    height: 400px;
    overflow: hidden;
    background: url("https://sbis.perm.ru/wp-content/uploads/2018/08/Geometric-Pattern-Wallpaper-7.jpg");
    background-size: 100%;
}

.container-cards {
    display: flex;
    flex-flow: row wrap;
    padding: 4rem;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
}

nav {
    padding-top: 10px;
    background: rgb(12, 36, 105);
}

.box {
    border: 6px white solid;
    background: rgba(0, 0, 100, 0.3);
    margin: 0 auto;
    max-width: 900px;
    padding: 36px;
}

.brand {
    color: white;
    font-family: 'Roboto Slab', serif;
    font-size: 24px;
}

.stats {
    color: rgb(170,170,170);
    font-family: 'Roboto', sans-serif;
    font-size: 24px;
}

.banner1 {
    color: white;
    font-family: 'Roboto Slab', serif;
    font-size: 24px;
}


.banner2 {
    color: white;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
}

a {
    color: white;
    text-decoration: none
}

a:visited {
    color: white;
    text-decoration: none
}

a:active {
    color: white;
    text-decoration: none
}

a:hover {
    color: white;
    text-decoration: none
}


